@use 'base/variables' as v;
@use 'base/mixins' as m;
.proyectos {
    display: block;
    margin-bottom: 2rem;
    video {
        width: 100%;
    }
    @include m.dektop {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    h3 {
        text-align: center;
    }
}