@use 'base/variables' as v;
@use 'base/mixins' as m;
.sobre-habilidades {
    padding-top: 2rem;
    padding-bottom: 3rem;
    background-image: url(../../../build/img/back_technical_skills.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    h2 {
        text-align: center;
        color: v.$blanco;
    }
}

.habilidades {
    gap: 2rem;
    @include m.resetear-lista;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    img {
        width: 50%;
        margin: 0 auto;
    }
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        img {
            width: 90%;
        }
    }
    justify-content: center;
    @include m.dektop {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        img {
            width: 90%;
        }
    }
}

.habilidades-segundas {
    @include m.resetear-lista;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    justify-content: center;
    margin-top: 2rem;
    img {
        width: 65%;
        margin: 0 auto;
    }
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        img {
            width: 90%;
        }
    }
    justify-content: center;
    @include m.dektop {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        img {
            width: 90%;
        }
    }
}