@use 'base/mixins' as m;
@use 'base/variables' as v;
.site-footer {
    background-image: url(../../../build/img/back_footer_laptop.jpg);
    background-position: center 70%;
    background-size: cover;
    background-repeat: no-repeat;
    height: 60rem;
}

.foteer {
    .imagen-banda-azul {
        width: 100%;
        height: 5rem;
        display: block;
    }
    position: relative;
    .overlay {
        position: absolute;
        background: rgb(29, 22, 22);
        background: linear-gradient(130deg, rgba(64, 64, 64, 0.8127626050420168) 0%, rgba(3, 3, 3, 0.835171568627451) 100%);
        width: 100%;
        height: 55rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

.contacto {
    h2 {
        color: v.$blanco;
        font-size: 3rem;
        margin: 3rem;
        text-align: center;
    }
    .copyright {
        color: v.$blanco;
        text-align: center;
        span {
            font-weight: 700;
        }
    }
    legend {
        font-weight: 400;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 2rem;
        text-align: center;
        width: 100%;
    }
    .formulario {
        @include m.tablet {
            display: grid;
            grid-template-columns: 50% 50%;
        }
        margin: 0 auto;
        display: block;
        fieldset {
            border: solid .2rem v.$blanco;
            margin: .5rem;
            display: grid;
            grid-template-columns: 30% 70%;
            padding: 0;
        }
        input {
            font-weight: 400;
            outline: none;
            border: none;
            height: 100%;
            width: 100%;
        }
        label {
            font-weight: 400;
            color: v.$blanco;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
        }
        textarea {
            display: block;
            width: 100%;
            background-color: v.$blanco;
            border: none;
            outline: none;
            height: 3rem;
        }
    }
    .alinear-derecha {
        display: flex;
        justify-content: end;
        margin: 1rem;
        .boton-formulario {
            font-weight: 700;
            background-color: v.$azuloscuro;
            font-size: 1.8rem;
            color: v.$blanco;
            padding: .5rem 2rem;
            border: none;
            border-radius: .3rem;
            &:hover {
                cursor: pointer;
                background-color: v.$azul;
                transition: background-color .5s ease;
            }
        }
    }
    .sitios {
        display: flex;
        justify-content: center;
        .barra-lateral {
            width: 10%;
            height: .5rem;
            margin-top: 3.5%;
            @include m.tablet {
                width: 10%;
                height: 1rem;
                margin-top: 2%;
            }
        }
        a {
            display: flex;
            width: 8%;
            @include m.tablet {
                width: 5%;
            }
            .logo {
                width: 100%;
            }
        }
        .logo {
            width: 8%;
            @include m.tablet {
                width: 5%;
            }
        }
    }
}


/* Mensaje de alerta*/

.error {
    margin: 1rem auto;
    background-color: v.$rojoweight;
    text-align: center;
    padding: .5rem;
    color: v.$blanco;
    font-weight: 400;
    @include m.dektop {
        width: 95%;
    }
}

.correcto {
    margin: 1rem auto;
    background-color: rgb(2, 124, 22);
    text-align: center;
    padding: .5rem;
    color: v.$blanco;
    font-weight: 400;
    margin: 2rem auto;
    @include m.dektop {
        width: 95%;
    }
}

.animacion {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}

.vacio {
    margin-bottom: 1rem;
}


/* Spinner */

.sk-chase {
    width: 40px;
    height: 40px;
    position: relative;
    animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    animation: sk-chase-dot 2.0s infinite ease-in-out both;
}

.sk-chase-dot:before {
    content: '';
    display: block;
    width: 25%;
    height: 25%;
    background-color: #fff;
    border-radius: 100%;
    animation: sk-chase-dot-before 2.0s infinite ease-in-out both;
}

.sk-chase-dot:nth-child(1) {
    animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
    animation-delay: -1.0s;
}

.sk-chase-dot:nth-child(3) {
    animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4) {
    animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5) {
    animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6) {
    animation-delay: -0.6s;
}

.sk-chase-dot:nth-child(1):before {
    animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
    animation-delay: -1.0s;
}

.sk-chase-dot:nth-child(3):before {
    animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4):before {
    animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5):before {
    animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6):before {
    animation-delay: -0.6s;
}

@keyframes sk-chase {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes sk-chase-dot {
    80%,
    100% {
        transform: rotate(360deg);
    }
}

@keyframes sk-chase-dot-before {
    50% {
        transform: scale(0.4);
    }
    100%,
    0% {
        transform: scale(1.0);
    }
}