html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #170b2a;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
  text-align: center;
  margin: 2rem;
  color: #0a4b96;
}

h3 {
  font-size: 2rem;
  color: #0a4b96;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-image: url(../../../build/img/header.png);
  background-position: left;
  background-size: cover;
  background-repeat: no-repeat;
  height: 45rem;
}
@media (min-width: 768px) {
  .header {
    height: 65rem;
    background-position: center center;
  }
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
  }
}
.header .contenido-header .imagen-logo {
  margin-left: 25%;
  width: 50%;
}
@media (min-width: 768px) {
  .header .contenido-header .imagen-logo {
    width: 50rem;
    margin-top: 15rem;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
  }
  .navegacion-principal.fijo {
    position: fixed;
    background-color: #0a4b96;
    width: 88%;
    box-shadow: 0.1rem 0.2rem 0.1rem #333;
  }
  .navegacion-principal.fijo a {
    background-position: 0% 100%;
  }
}
.navegacion-principal a {
  padding: 1rem;
  display: block;
  text-align: center;
  text-decoration: none;
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 2px;
  transition: background-size 0.7s;
  color: #ffffff;
  font-size: 2rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    background-position: 0% 12%;
    margin-top: 1rem;
  }
}
.navegacion-principal a:hover, .navegacion-principal a:focus {
  background-size: 100% 2px;
}

.site-footer {
  background-image: url(../../../build/img/back_footer_laptop.jpg);
  background-position: center 70%;
  background-size: cover;
  background-repeat: no-repeat;
  height: 60rem;
}

.foteer {
  position: relative;
}
.foteer .imagen-banda-azul {
  width: 100%;
  height: 5rem;
  display: block;
}
.foteer .overlay {
  position: absolute;
  background: #1d1616;
  background: linear-gradient(130deg, rgba(64, 64, 64, 0.812762605) 0%, rgba(3, 3, 3, 0.8351715686) 100%);
  width: 100%;
  height: 55rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contacto h2 {
  color: #ffffff;
  font-size: 3rem;
  margin: 3rem;
  text-align: center;
}
.contacto .copyright {
  color: #ffffff;
  text-align: center;
}
.contacto .copyright span {
  font-weight: 700;
}
.contacto legend {
  font-weight: 400;
  color: #ffffff;
  font-size: 2.4rem;
  margin-bottom: 2rem;
  text-align: center;
  width: 100%;
}
.contacto .formulario {
  margin: 0 auto;
  display: block;
}
@media (min-width: 768px) {
  .contacto .formulario {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}
.contacto .formulario fieldset {
  border: solid 0.2rem #ffffff;
  margin: 0.5rem;
  display: grid;
  grid-template-columns: 30% 70%;
  padding: 0;
}
.contacto .formulario input {
  font-weight: 400;
  outline: none;
  border: none;
  height: 100%;
  width: 100%;
}
.contacto .formulario label {
  font-weight: 400;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.contacto .formulario textarea {
  display: block;
  width: 100%;
  background-color: #ffffff;
  border: none;
  outline: none;
  height: 3rem;
}
.contacto .alinear-derecha {
  display: flex;
  justify-content: end;
  margin: 1rem;
}
.contacto .alinear-derecha .boton-formulario {
  font-weight: 700;
  background-color: #0a4b96;
  font-size: 1.8rem;
  color: #ffffff;
  padding: 0.5rem 2rem;
  border: none;
  border-radius: 0.3rem;
}
.contacto .alinear-derecha .boton-formulario:hover {
  cursor: pointer;
  background-color: #0000f4;
  transition: background-color 0.5s ease;
}
.contacto .sitios {
  display: flex;
  justify-content: center;
}
.contacto .sitios .barra-lateral {
  width: 10%;
  height: 0.5rem;
  margin-top: 3.5%;
}
@media (min-width: 768px) {
  .contacto .sitios .barra-lateral {
    width: 10%;
    height: 1rem;
    margin-top: 2%;
  }
}
.contacto .sitios a {
  display: flex;
  width: 8%;
}
@media (min-width: 768px) {
  .contacto .sitios a {
    width: 5%;
  }
}
.contacto .sitios a .logo {
  width: 100%;
}
.contacto .sitios .logo {
  width: 8%;
}
@media (min-width: 768px) {
  .contacto .sitios .logo {
    width: 5%;
  }
}

/* Mensaje de alerta*/
.error {
  margin: 1rem auto;
  background-color: #cf0000;
  text-align: center;
  padding: 0.5rem;
  color: #ffffff;
  font-weight: 400;
}
@media (min-width: 992px) {
  .error {
    width: 95%;
  }
}

.correcto {
  margin: 1rem auto;
  background-color: #027c16;
  text-align: center;
  padding: 0.5rem;
  color: #ffffff;
  font-weight: 400;
  margin: 2rem auto;
}
@media (min-width: 992px) {
  .correcto {
    width: 95%;
  }
}

.animacion {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.vacio {
  margin-bottom: 1rem;
}

/* Spinner */
.sk-chase {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  animation: sk-chase-dot 2s infinite ease-in-out both;
}

.sk-chase-dot:before {
  content: "";
  display: block;
  width: 25%;
  height: 25%;
  background-color: #fff;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s infinite ease-in-out both;
}

.sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3) {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4) {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5) {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6) {
  animation-delay: -0.6s;
}

.sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3):before {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4):before {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5):before {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6):before {
  animation-delay: -0.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4);
  }
  100%, 0% {
    transform: scale(1);
  }
}
.sobre-habilidades {
  padding-top: 2rem;
  padding-bottom: 3rem;
  background-image: url(../../../build/img/back_technical_skills.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.sobre-habilidades h2 {
  text-align: center;
  color: #ffffff;
}

.habilidades {
  gap: 2rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  justify-content: center;
  justify-content: center;
}
.habilidades img {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .habilidades {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .habilidades img {
    width: 90%;
  }
}
@media (min-width: 992px) {
  .habilidades {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  .habilidades img {
    width: 90%;
  }
}

.habilidades-segundas {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  justify-content: center;
  margin-top: 2rem;
  justify-content: center;
}
.habilidades-segundas img {
  width: 65%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .habilidades-segundas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .habilidades-segundas img {
    width: 90%;
  }
}
@media (min-width: 992px) {
  .habilidades-segundas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .habilidades-segundas img {
    width: 90%;
  }
}

.sobre {
  display: block;
  margin-bottom: 5rem;
}
@media (min-width: 992px) {
  .sobre {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}
.sobre .imagen {
  width: 100%;
  margin: 0 auto;
}
.sobre .imagen .front {
  background-color: #0a4b96;
  width: 90%;
  box-shadow: 0.8rem 0.8rem 0.5rem #464545;
}
.sobre .contenido-acerca {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sobre .contenido-acerca h3 {
  margin-top: 2rem;
  padding-top: 0.5rem;
}

@keyframes mover {
  0% {
    transform: translate(50%);
  }
}
.proyectos {
  display: block;
  margin-bottom: 2rem;
}
.proyectos video {
  width: 100%;
}
@media (min-width: 992px) {
  .proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.proyectos h3 {
  text-align: center;
}