@use 'base/variables' as v;
@use 'base/mixins' as m;
.sobre {
    display: block;
    margin-bottom: 5rem;
    @include m.dektop {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;
    }
    .imagen {
        width: 100%;
        margin: 0 auto;
        .front {
            background-color: v.$azuloscuro;
            width: 90%;
            box-shadow: .8rem .8rem .5rem rgb(70, 69, 69);
        }
    }
    .contenido-acerca {
        display: flex;
        flex-direction: column;
        justify-content: center;
        h3 {
            margin-top: 2rem;
            padding-top: .5rem;
        }
    }
}

@keyframes mover {
    0% {
        transform: translate(50%);
    }
}